import React, { Component } from 'react';

class HomeContainer extends Component {

  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className="container">
        <div className="page centered">
          This is home.
        </div>
      </div>
    );
  }

}

export default HomeContainer;



